@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700;800&display=swap);
body,.App{height:100vh;width:100%;margin:0px;background:linear-gradient(to top, #7A5B6E, #423551);color:white;font-family:'Montserrat';-webkit-user-select:none;-ms-user-select:none;user-select:none;overflow:hidden}.App{display:flex;flex-direction:column;justify-content:center;align-items:center}.App .slider{display:flex;flex-direction:row;flex-basis:auto;max-width:100%;padding:20px;overflow-x:auto;-webkit-overflow-scrolling:touch;scroll-behavior:smooth}.App .slider::-webkit-scrollbar{height:8px;border-radius:10px;background-color:rgba(255,255,255,0)}.App .slider::-webkit-scrollbar-thumb{background-color:rgba(255,255,255,0.699);border-radius:10px}.App .footer{position:absolute;bottom:2px;font-size:15px;font-weight:300;margin:0px;animation:fadeIn ease-in 1s forwards}.App .footer img{width:15px;vertical-align:middle}.App .footer a{color:#E57873;text-decoration:none}.alert{display:flex;flex-direction:row;align-items:center;justify-content:center;background-color:rgba(255,177,177,0.116);border-radius:4px;height:40px;opacity:0%}.alert img{width:24px;height:24px;padding:0px 10px;-webkit-user-select:none;-ms-user-select:none;user-select:none}.alert img:last-of-type{transition:all 0.5s ease;opacity:60%}.alert img:last-of-type:hover{opacity:100%}.alert p{font-size:12px;font-weight:300;margin:0px}.fade-in{animation:fadeIn ease-in 1s forwards}.fade-in .img:last-of-type{cursor:pointer}.fade-out{animation:fadeOut ease-in 1s forwards}@keyframes fadeIn{from{opacity:0%}to{opacity:100%}}@keyframes fadeOut{from{opacity:100%}to{opacity:0%}}

.Weather{display:flex;min-width:400px;flex-direction:row;align-items:center;justify-content:center;margin:25px 15px;animation:fadeIn ease-in 1s forwards;border-radius:10px;background:var(--day-cycle-bg)}.Weather .card{display:flex;flex-direction:column;align-items:center;justify-content:space-between;flex-basis:400px;height:600px;box-shadow:rgba(0,0,0,0.25) 0px 14px 28px,rgba(0,0,0,0.22) 0px 10px 10px;border-radius:10px;overflow:hidden;animation:fadeIn ease-in 1s forwards}.Weather .card .top{display:flex;flex-direction:column;align-items:center;width:100%}.Weather .card .top .close-component{width:32px;margin-bottom:25px;margin-left:auto;margin-right:5px;margin-top:5px;cursor:pointer;opacity:50%;transition:all 0.5s ease;-webkit-user-select:none;-ms-user-select:none;user-select:none}.Weather .card .top .close-component:hover{opacity:100%}.Weather .card .top h1{font-size:60px;font-weight:300;margin:0px}.Weather .card .top h2{font-weight:300;margin:0px;text-transform:capitalize}.Weather .card .top h3{font-weight:400;margin:0px}.Weather .card .top p{font-size:12px;font-weight:300;padding-top:2px;margin:0px;margin-bottom:10px;opacity:60%}.Weather .card .middle .sun-viewbox{display:flex;flex-direction:row;justify-content:center;align-items:center;width:75px;height:75px}.Weather .card .middle .sun-viewbox .sun{bottom:0px;z-index:0;width:var(--day-cycle-sun-size);height:var(--day-cycle-sun-size);background-color:var(--day-cycle-sun);margin-bottom:0px;border-radius:50%;box-shadow:var(--day-cycle-sun-box)}.Weather .card .bot{width:100%;height:100%;display:contents}.Weather .card .bot svg{display:flex;flex-direction:column;align-items:center;width:110%;margin-bottom:-30px;z-index:1}.Weather .card .bot svg #mount{fill:var(--day-cycle)}.Weather .card .bot svg .cls-1{fill:none}.Weather .card .bot svg .cls-2{fill:url(#Dégradé_sans_nom_13)}.Weather .card .bot svg .cls-3{fill:url(#Dégradé_sans_nom_13-2)}.Weather .card .bot svg .cls-4{fill:url(#Dégradé_sans_nom_13-3)}.Weather .card .bot svg .cls-5{fill:url(#Dégradé_sans_nom_13-4)}.Weather .card .bot svg .cls-6{fill:url(#Dégradé_sans_nom_13-5)}.Weather .card .bot .footer-content{display:flex;flex-wrap:wrap;align-content:center;flex-direction:column;width:360px;height:150px;padding:0px 20px;z-index:2;background-color:var(--day-cycle)}.Weather .card .bot .footer-content .top{display:flex;flex-direction:row;justify-content:space-between;align-content:space-between}.Weather .card .bot .footer-content .top h3{font-size:15px;font-weight:400;font-family:'Montserrat';background-color:rgba(0,0,0,0);color:white;border:none;text-transform:capitalize;margin:0px 10px}.Weather .card .bot .footer-content .top div{display:flex;flex-direction:row}.Weather .card .bot .footer-content .top div svg{cursor:pointer;margin:0px 4px;opacity:50%;transition:all 0.5s ease}.Weather .card .bot .footer-content .top div svg:hover{opacity:100%}.Weather .card .bot .footer-content .top div .active-svg{opacity:100%}.Weather .card .bot .footer-content .line{margin:5px 0px;border-top:1px solid #d3d3d3;width:100%}.Weather .card .bot .footer-content .bot .hide{display:none;transition:all 0.5s ease}.Weather .card .bot .footer-content .bot .tab-temp{display:flex;flex-direction:row;justify-content:space-between;overflow:scroll;transition:all 0.5s ease}.Weather .card .bot .footer-content .bot .tab-temp::-webkit-scrollbar{display:none}@keyframes fadeIn{from{opacity:0%}to{opacity:100%}}@keyframes fadeIn50{from{opacity:50%}to{opacity:100%}}

.weather-wrap{display:flex;flex-direction:column;margin:0px 10px;align-items:center}.weather-wrap h3{font-size:15px;font-weight:500;margin:10px 0px}.weather-wrap img{width:35px;height:35px;-webkit-user-select:none;-ms-user-select:none;user-select:none}

.average-wrap{display:flex;flex-direction:column;margin:0px 10px;align-items:center;justify-content:space-between;transition:all 0.5s ease}.average-wrap .col{width:100%}.average-wrap .col ul{display:flex;flex-direction:row;margin:0px;padding:0px;list-style:none;justify-content:space-between}.average-wrap .col ul li h3{font-weight:300;font-size:10px;opacity:60%;margin:0px}.average-wrap .col ul li p{font-weight:300;font-size:16px;margin:0px}.average-wrap .line{opacity:40%;margin:3px 0px !important}

.settings-wrap{display:flex;flex-direction:row;margin:10px;align-items:center;justify-content:space-between}.settings-wrap .right{text-align:end}.settings-wrap ul{list-style:none;margin:0px;padding:0px}.settings-wrap ul li{font-weight:400;font-size:15px}.settings-wrap ul li img{display:inline;width:20px;vertical-align:middle;cursor:pointer;transition:all 0.5s ease;opacity:50%}.settings-wrap ul li span{font-weight:300;font-size:15px;opacity:50%;cursor:pointer;transition:all 0.5s ease}.settings-wrap ul li span a{color:white}.settings-wrap ul li .active{opacity:100%}

.Loader{display:flex;flex-direction:column;align-items:center}.Loader h1{font-weight:300;font-size:18px;margin:0px}.Loader h1 span:first-of-type{animation:.4s ease-in 0s infinite alternate fadeIn}.Loader h1 span{animation:.4s ease-in .2s infinite alternate fadeIn}.Loader h1 span:last-of-type{animation:.4s ease-in .4s infinite alternate fadeIn}.Loader img{margin-bottom:20px;width:80px}@keyframes fadeIn{0%{opacity:0%}50%{opacity:100%}100%{opacity:100%}}

.wrapper-input{display:flex;flex-direction:column;align-items:center;justify-content:center;min-width:400px}.wrapper-input form{display:flex;flex-direction:column;align-items:center;justify-content:center;margin:20px 0px}.wrapper-input form label{margin:2px;font-size:20px;font-weight:300;font-family:'Montserrat'}.wrapper-input form input{font-size:15px;font-weight:300;text-align:center;font-family:'Montserrat';background-color:rgba(255,255,255,0);color:white;border:none;padding:10px;border-bottom:2px solid lightgrey}.wrapper-input form input:focus{outline:none;box-shadow:none}

